import React, {Component} from 'react'
import Converstation from '../model/converstation'
import Messenger from './messenger'

export default class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            Converstation: new Converstation(this),
        }
    }

    render() {
        const {Converstation} = this.state;
        return <div className="app-wrapper">
            <Messenger store={Converstation}/>
        </div>
    }
}


